/* All */

html,
body {
  height: 100%;
}

.app-body {
  padding-top: 65px;
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f0f2f5;
  font-family: "Montserrat", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
  color: #fff;
}

a:hover {
  text-decoration: none;
  color: #fff;
}

.center {
  margin: auto;
  text-align: center;
  align-items: center;
}

.center-row {
  justify-content: center;
}

.title {
  font-family: "Oswald", sans-serif;
}

.top-space {
  margin-top: 50px;
}

.bottom-space {
  margin-bottom: 0px;
}

@media only screen and (max-width: 720px) {
  .app-body {
    padding-top: 30px;
  }

  .top-space {
    margin-top: 0;
  }

  .bottom-space {
    margin-bottom: 120px;
  }
}

/* Login */

.logo-wrapper {
  padding: 90px 0px 20px;
}

.ceco-logo {
  border-radius: 8px;
  width: 350px;
}

.login-title {
  font-size: 100px;
}

.login-subtitle {
  font-size: 25px;
}

.card-body {
  width: 80%;
}

.btn {
  background-color: #f14a24;
  color: #fff;
}
.btn:hover {
  background-color: #da4321;
  color: #fff;
}

.floating {
  align-items: center;
  border: none;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  margin: 40px 0 0;
  padding: 20px 0 28px;
  /* width: 396px; */
}

/* media queries */

/* column break */
@media only screen and (max-width: 720px) {
  .logo-wrapper {
    padding-top: 0;
  }
}

/* =========================== */

/* Navbar */

.navbar {
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  height: 80px;
}

.nav {
  flex-wrap: nowrap;
}

.nav-link {
  color: #148fcb;
}

.nav-link:hover {
  color: #f14a24;
}
.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  background-color: #148fcb;
}

.nav-icon {
  display: none;
}

/* column break */
@media only screen and (max-width: 720px) {
  .navbar {
    box-shadow: 2px 1px 12px 12px rgb(0 0 0 / 10%);
    height: 80px;
  }

  .fixed-top {
    position: fixed;
    bottom: 0;
    z-index: 1030;
    top: auto;
  }

  .nav-text {
    display: none;
  }

  .nav-icon {
    display: inline-flex;
    font-size: 1.75rem;
  }
}

/* =========================== */

/* Profile */

.profile-pic-container {
  text-align: center;
}

.profile-pic {
  width: 300px;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
}

.profile-info {
  width: 300px;
  text-align: left;
  margin: auto;
  padding-top: 20px;
}

.badge {
  background-color: #f14a24;
}

.profile-geo {
  margin-left: 10px;
}

/* media queries */
@media only screen and (max-width: 720px) {
  .profile-info {
    padding: 20px 0px 40px;
  }

  .profile-pic,
  .profile-description {
    margin-top: 0;
  }
}

/* =========================== */

/* Contact List */

.filter-button {
  width: 150px;
  font-size: 13px;
}

.filter-btn-container {
  text-align: right;
}

.filter-text {
  margin-left: 5px;
}

.list-group-item:hover {
  background-color: #f14a24;
  color: white;
}

/* =========================== */

/* Newsfeed */

.post {
  border: none;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  margin: 15px 0 0;
  padding: 10px;
  /* width: 396px; */
}

.post-header {
  justify-content: left;
  vertical-align: auto;
  padding-bottom: 10px;
}

.post-prof-pic,
.user {
  display: inline-block;
}

.post-prof-pic {
  width: 40px;
  background-color: #ffdfd7;
  border-radius: 100%;
}

.user {
  margin-left: 15px;
  font-weight: bold;
}

.post-body {
  font-size: 12px;
  padding: 0px 25px 0px 25px;
}

.geo-tag-container {
  float: right;
  text-align: right;
}

.geo-tag {
  margin-left: 15px;
  font-size: 10px;
}

/* =========================== */

/* Events */

.event-title {
  display: inline-block;
  font-weight: bold;
}

.event-img {
  border-radius: 8px;
  margin-bottom: 10px;
}

.event-body {
  font-size: 13px;
  padding: 0px 20px 0px 20px;
}

.event-datetime {
  color: #f14a24;
  font-weight: bold;
}

.event-btn {
  float: right;
  width: 150px;
  font-size: 13px;
  margin-bottom: 10px;
}

@media only screen and (max-width: 720px) {
  .event-title {
    font-size: 15px;
  }
}

/* =========================== */

/* Enroll */

.form-shadow {
  border: none;
  border-radius: 8px;
  box-shadow: 0 2px 4px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
  padding: 20px 0 28px;
  align-items: center;
}

.form-title {
  text-align: center;
  font-weight: bolder;
}

.form-subtitle {
  font-weight: bold;
}

.form-btn {
  /* float: right; */
  width: 150px;
}

.center-title {
  text-align: center;
}

.steps {
  margin-bottom: 20px;
}
